<style scoped>
img {width: 2.5rem;height: 2.5rem;border-radius: 50%;margin-right: 0.5rem;}
</style>>
<template>
<div class="bg">
  <div class="content" v-if="!editShow">
   <Search user='true'></Search>
    <el-table
      :data="tableData"
       style="width: 100%;margin-top:0.5rem" :cell-style="{fontSize:'0.65rem'}" :header-cell-style="{background:'#eef1f6',color:'#606266',fontSize:'0.6rem'}">
      <el-table-column  align="center" 
        label="会员ID"
      >
        <template slot-scope="scope">
          <span >98</span>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="会员"
        >
        <template slot-scope="scope">
            <div class="row">
            <img src="http://t8.baidu.com/it/u=3571592872,3353494284&fm=79&app=86&size=h300&n=0&g=4n&f=jpeg?sec=1581766834&t=b367d43ed4ab7e981ce884ba40843051" /><span class="pl10">{{ scope.row.name }}</span>
            </div>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="常用收货地址"
      >
        <template slot-scope="scope">
          <span >{{ scope.row.address }}</span>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="积分/余额"
        >
        <template slot-scope="scope">
          <p >积分：{{ scope.row.num1 }}</p>
          <p >余额：{{ scope.row.num2 }}</p>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="成交"
        >
        <template slot-scope="scope">
          <p >订单：{{ scope.row.num1 }}</p>
          <p >金额：{{ scope.row.num2 }}</p>
        </template>
      </el-table-column>
      <el-table-column  align="center" 
        label="注册时间"
      >
        <template slot-scope="scope">
          <span >{{ scope.row.date }}</span>
        </template>
      </el-table-column>
      <el-table-column  align="center"  label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="hEdit(1)">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="p20 textRight">
      <el-pagination  
        @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage1"
            :page-size="100"
            layout="total,prev, pager, next, jumper"
            :total="1000">
        </el-pagination>
    </div>
  </div>
  <!-- 编辑用户信息 -->
  <div class="content p20" v-if="editShow">
    <div class="blue mb20 back" @click="back">
      <i class="iconfont icon-cs-fh-1 "></i><a class="font18 pl20">返回</a>
    </div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm p20">
      <el-form-item label="会员" required>
         <img src="http://t8.baidu.com/it/u=3571592872,3353494284&fm=79&app=86&size=h300&n=0&g=4n&f=jpeg?sec=1581766834&t=b367d43ed4ab7e981ce884ba40843051" /> <span  class="pl10" >{{ruleForm.name}}</span>
          
      </el-form-item>
      <el-form-item label="openId" prop="name">
       <span class="blue">wap_user_3_19906722716</span>
      </el-form-item>
      <el-form-item label="真实姓名" >
        <el-input v-model="ruleForm.name"></el-input>
      </el-form-item>
      <el-form-item label="微信号" >
        <el-input v-model="ruleForm.nickname"></el-input>
      </el-form-item>
      <el-form-item label="手机号码" >
        <el-input v-model="ruleForm.tel"></el-input>
      </el-form-item>
      <el-form-item label="用户密码" required>
       <span class="lightBlue pl10">修改</span>
      </el-form-item>
      <el-form-item label="积分" >
       <span class="blue">0</span>
      <span class="lightBlue pl10">修改</span>
      </el-form-item>
      <el-form-item label="余额" >
       <span class="blue">0</span>
       <span class="lightBlue pl10">修改</span>
      </el-form-item>
      <el-form-item label="注册时间" >
       <span >2020-01-21 16:36:49</span>
      </el-form-item>
      <el-form-item label="黑名单" prop="resource">
        <el-radio-group v-model="ruleForm.resource">
          <el-radio label="是"></el-radio>
          <el-radio label="否"></el-radio>
        </el-radio-group>
        <span class="c9 font10 pl20 ">注：设置黑名单后，此会员无法访问该系统</span>
      </el-form-item>
      
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
        <el-button @click="back">返回列表</el-button>
      </el-form-item>
    </el-form>
  </div>
</div>
</template>

<script>
import Search from '@/components/search'
  export default {
    data() {
      return {
        tableData: [{
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          num1:1,
          num2:1
          
        }, {
          date: '2016-05-04',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1517 弄',
           num1:1,
          num2:1
        }, {
          date: '2016-05-01',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1519 弄',
           num1:1,
          num2:1
        }, {
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1516 弄',
           num1:1,
          num2:1
        }],
          currentPage1: 5,
        currentPage2: 5,
        currentPage3: 5,
        currentPage4: 4,
        editShow:false,
        ruleForm: {
          name: '',
          nickname: '',
          tel: '',
          resource: '',
         
        },
        rules: {
          name: [
            { required: true, message: '请输入真实姓名', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          tel: [
            { required: true, message: '请输入电话号码', trigger: 'change' }
          ],
          nickname: [
            { required: true, message: '请输入微信号码', trigger: 'change' }
          ],
          
        }
      }
    },
    methods: {
      hEdit(index) {
        console.log(index);
        var that=this;
        that.editShow=true;
      },
      handleDelete(index, row) {
        console.log(index, row);
      },
      handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      back() {
        this.editShow=false;
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
      
    },
     //注册组件
    components: {
      Search,
     
    },
  }
</script>


